import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {

  render() {
    const data = this.props.data ? this.props.data : [];

    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.fixed_asset ? data.fixed_asset.location.location_name : null 
      },
      {
        label: <IntlMessages id="departments.pageHeader" />,
        description: data.fixed_asset ? data.fixed_asset.department.department_name : null 
      },
      {
        label: <IntlMessages id="fixedAssetCode" />,
        description: <strong>{ data.fixed_asset ? data.fixed_asset.fixed_asset_code : null} </strong>
      },
      {
        label: <IntlMessages id="fixedAssetName" />,
        description: <strong>{data.fixed_asset ? data.fixed_asset.fixed_asset_name : null} </strong>
      },
      {
        label: <IntlMessages id="fixedAssets.add.serialNo" />,
        description: data.fixed_asset ? data.fixed_asset.serial_no : null
      },
      {
        label: <IntlMessages id="assetValue" />,
        description: data.fixed_asset 
        ? <NumberFormat value={data.fixed_asset.asset_value} />
        : null
      }, 
      
    ];

    const rightSide = [
      {
        label: <IntlMessages id="acquisitionDate" />,
        description:
          data.fixed_asset
            ? moment(
                data.fixed_asset.acquisition_date,
                "YYYY-MM-DD HH:mm:ss"
              ).format("DD MMMM YYYY")
            : null
        
      },
      {
        label: <IntlMessages id="depreciationEndDate" />,
        description:
          data.fixed_asset
            ? moment(
                data.fixed_asset.depreciation_end_date,
                "YYYY-MM-DD HH:mm:ss"
              ).format("DD MMMM YYYY")
            : null
        
      },
      {
        label: <IntlMessages id="assetAge" />,
        description: data.fixed_asset 
          ? moment(new Date(), "YYYY-MM-DD").diff(moment(data.fixed_asset.acquisition_date, "YYYY-MM-DD"), "months")
          : null
      }, 
      {
        label: <IntlMessages id="estimatedLife" />,
        description: data.fixed_asset 
          ? data.fixed_asset.estimated_life_year*12 + data.fixed_asset.estimated_life_month
          : null
      }, 
      {
        label: <IntlMessages id="quantity" />,
        description: data.fixed_asset 
          ? <NumberFormat value={data.fixed_asset.qty} />
          : null
      }, 
      // {
      //   label: <IntlMessages id="salvageValue" />,
      //   description: data.fixed_asset 
      //     ? <NumberFormat value={data.fixed_asset.salvage_value} />
      //     : null
      // }, 
      {
        label: <IntlMessages id="monthlyDepreciation" />,
        description: data.fixed_asset 
          ? <NumberFormat value={data.fixed_asset.monthly_depreciation} />
          : null
      }, 
      {
        label: <IntlMessages id="valuePerQuantity" />,
        description: data.fixed_asset 
        ?
          data.fixed_asset.qty > 0 
            ? <NumberFormat value={parseFloat(data.fixed_asset.book_value/data.fixed_asset.qty)} />
            : 0
        : null
      }, 
      {
        label: <IntlMessages id="bookValue" />,
        description: data.fixed_asset 
          ? <NumberFormat value={data.fixed_asset.book_value} />
          : null
      }, 

    ];



    const bottomLeftSide = [
      {
        label: <IntlMessages id="transferDate" />,
        description:
          data.transfer_date
            ? moment(
                data.transfer_date,
                "YYYY-MM-DD HH:mm:ss"
              ).format("DD MMMM YYYY")
            : null
        
      },
      {
        label: <IntlMessages id="quantity" />,
        description: <NumberFormat value={data.qty} />
      }, 
      {
        label: <IntlMessages id="fromLocation" />,
        description: data.from_location_name
      },
      {
        label: <IntlMessages id="toLocation" />,
        description: data.to_location_name
      }, 
    ];

    const bottomRightSide = [
      {
        label: <IntlMessages id="note" />,
        description: data.note
      }, 
    ];


    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.fixedAssetTransfer" />}
          subtitle={
            <>
              <Row>
                <Col>
                  {/* <strong>{data.transaction_no}</strong> */}
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transfer_date
                        ? moment(
                            data.transfer_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="fixedAssetTransfer.subtitle.transfer"
                style={{ marginTop: "-10" }}
              />
            </Divider>
              <FieldCol leftSide={bottomLeftSide} rightSide={bottomRightSide} />
          </Row>

          {/* <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row> */}

          <Row style={{ marginTop: "50px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => document.title = getFileName("fixed_asset_transfer", null, false)}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
        />
      </>
    );
  }
}

export default Print;
