import React, { Component } from "react";
import moment from "moment"
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { 
  Form, Input, Select, Col, InputNumber, DatePicker, 
  Popconfirm, Steps, Row, Tabs, Divider
} from "antd";
import { customDot } from "../../../App/app.style";
import ModalCancel from "./modalCancel";
import getLanguage from "../../../App/MyLanguange";

const TabPane = Tabs.TabPane;
const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class FixedAssetTransferUpdateForm extends Component {
  state={
    check:2,
    id_fixed_asset: null,
    visibleModalCancel: false,
  }

  componentDidUpdate = () => {

    if(this.state.check === 2 && this.props.fixedAssetTransfer.fixed_asset_transfer !== null){
      const { fixed_asset_transfer } = this.props.fixedAssetTransfer;
      const { fixed_asset } = fixed_asset_transfer;
      const { setFieldsValue } = this.props.form;


      let estimated_life = fixed_asset.estimated_life_year * 12 + fixed_asset.estimated_life_month
      let asset_age =  moment(new Date(), "YYYY-MM-DD").diff(moment(fixed_asset.acquisition_date, "YYYY-MM-DD"), "months")


      setFieldsValue({
        id_from_location: fixed_asset.id_location,
        id_department: fixed_asset.id_department,
        fixed_asset_code: fixed_asset.fixed_asset_code,
        fixed_asset_name: fixed_asset.fixed_asset_name,
        serial_no: fixed_asset.serial_no,
        asset_value_fa: parseFloat(fixed_asset.asset_value),
        //acquisition_date: moment(fixed_asset.acquisition_date, "YYYY-MM-DD HH:mm:ss"),
        acquisition_date: moment(fixed_asset.acquisition_date, "YYYY-MM-DD"),
        depreciation_end_date: moment(fixed_asset.depreciation_end_date, "YYYY-MM-DD"),
        asset_age : estimated_life - asset_age > 0 ? asset_age : estimated_life ,
        estimated_life,
        qty_fa: fixed_asset.qty,
        monthly_depreciation_fa: fixed_asset.monthly_depreciation,
        book_value_fa: parseFloat(fixed_asset.book_value),
        depreciation_value_from_fa: parseFloat(fixed_asset.depreciation_value),
        // Transfer Info
        transfer_date: moment(fixed_asset_transfer.transfer_date, "YYYY-MM-DD HH:mm:ss"),
        id_to_location: fixed_asset_transfer.id_to_location,
        qty: fixed_asset_transfer.qty,
        note: fixed_asset_transfer.note,
        
      })

      this.setState({
        id_fixed_asset : fixed_asset_transfer.id_fixed_asset,
        check: 1
      })
    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      // console.log(err)
      if(!err) {
        values.transfer_date = values.transfer_date.format("YYYY-MM-DD HH:mm:ss");
        values.id_fixed_asset = this.state.id_fixed_asset;
        this.props.submit(values);
      }
    });
  };


  render() {
     // console.log(this.props);
     const { getFieldDecorator, getFieldValue } = this.props.form;
     const { fixedAssetTransfer, processFixedAssetTransfer } = this.props;
     const { loading } = fixedAssetTransfer;
     
     const data = fixedAssetTransfer.fixed_asset_transfer ? fixedAssetTransfer.fixed_asset_transfer : [];

     let permissions = this.props.permissions;
 
     // const error = fixedAssetTransfer.errorField ? fixedAssetTransfer.errorField : [];
     // console.log(data);
 
     const formItemLayout = {
       labelCol: {
         xs: { span: 24 },
         sm: { span: 24 }
       },
       wrapperCol: {
         xs: { span: 24 },
         sm: { span: 24 }
       }
     };
     const tailFormItemLayout = {
       wrapperCol: {
         xs: {
           span: 24,
           offset: 0
         },
         sm: {
           span: 24,
           offset: 0
         }
       }
     };
 
     return (
       <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
         {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="fixedAssets.add.subtitle.data" />
         </Divider> */}
         <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small" 
              progressDot={customDot} 
              current={
                data.status -1
              }
            >
              <Step title={getLanguage({id: "new"})} />
              <Step title={getLanguage({id: "processed"})}  />
              <Step title={getLanguage({id: "cancelled"})}  />
            </Steps>
          </Col>
        </Row>
        
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="fixedAssetTransfer.subtitle.fixedAsset" />
        </Divider>
        
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_from_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      //onChange={this.handleSelectLocation}
                      style={{ width:"100%" }}
                      disabled={true}
                    >
                      {fixedAssetTransfer.from_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          { data.location_code + " (" + data.location_name +")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="departments.pageHeader" />}
                >
                  {getFieldDecorator("id_department", {
                    // initialValue: data.id_department,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width:"100%" }}
                      disabled={true}
                    >

                      { fixedAssetTransfer.departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          { data.department_code + " (" + data.department_name +")"}
                        </Option>     
                        ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssets.add.code" />}
                >
                  {getFieldDecorator("fixed_asset_code", {
                    //initialValue: data.fixed_asset_code,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssetTransfers.add.name" />}
                >
                  {getFieldDecorator("fixed_asset_name", {
                    //initialValue: data.fixed_asset_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input 
                        disabled={true} 
                      />)}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssetTransfers.add.serialNo" />}
                >
                  {getFieldDecorator("serial_no", {
                    //initialValue: data.serial_no,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input 
                      disabled={true}
                    />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetValue" />}
                >
                  {getFieldDecorator("asset_value_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      min={0}
                      precision={2}
                      disabled={true}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            

          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            
            <InputGroup style={{ marginBottom:"-5px" }}>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="acquisitionDate" />
                  }
                >
                  {getFieldDecorator("acquisition_date", {
                    //initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%", padding: "0px" }}
                      format="DD MMMM YYYY"
                      disabled={true}
                      allowClear={false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="depreciationEndDate" />
                  }
                >
                  {getFieldDecorator("depreciation_end_date", {
                    //initialValue: moment().add(12, "month"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%", padding:"0px" }}
                      format="DD MMMM YYYY"
                      allowClear={false}
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetAge" />}
                >
                  {getFieldDecorator("asset_age", {
                    //initialValue: data.fixed_asset_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="estimatedLife" />}
                >
                  {getFieldDecorator("estimated_life", {
                    //initialValue: data.estimated_life,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>     
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="quantity" />}
                >
                  {getFieldDecorator("qty_fa", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="monthlyDepreciation" />}
                >
                  {getFieldDecorator("monthly_depreciation_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      //onChange= {this.handleChangeFromEstimatedLifeYear}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>     
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="bookValue" />}
                >
                  {getFieldDecorator("book_value_fa", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accumulationOfDepreciation" />}
                >
                  {getFieldDecorator("depreciation_value_from_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}                      
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          
          </div>  
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="fixedAssetTransfer.subtitle.transfer" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup style={{ marginBottom:"-5px" }}>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="transferDate" />
                  }
                >
                  {getFieldDecorator("transfer_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      // onChange={this.handleChangeTransDate}
                      // onOpenChange={this.handleChangeTransDate}
                      disabled={
                        permissions.includes("Can Back Date") && data.status ===1
                          ? false
                          : true
                      }
                      allowClear={false}
                      disabledDate={e => 
                        getFieldValue("acquisition_date")
                          ? e.valueOf() > moment() || e.valueOf() < moment(getFieldValue("acquisition_date"), "YYYY-MM-DD HH:mm:ss")  
                          : e.valueOf() > moment()       
                      }
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="quantity" />}
                >
                  {getFieldDecorator("qty", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={ data.status === 1 ? false : true }
                      min={0.01}
                      max={getFieldValue('qty_fa') ? parseFloat(getFieldValue('qty_fa')) : 999999999999999}
                      precision={2}
                      //onChange={this.handleChangeQuantity}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fromLocation" />}
                >
                  {getFieldDecorator("id_from_location", {
                    // initialValue: data.id_from_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      //onChange={this.handleSelectLocation}
                      style={{ width:"100%" }}
                      disabled={true}
                    >
                      {fixedAssetTransfer.from_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          { data.location_code + " (" + data.location_name +")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="toLocation" />}
                >
                  {getFieldDecorator("id_to_location", {
                    // initialValue: data.id_to_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width:"100%" }}
                      disabled={ data.status === 1 ? false : true }
                    >

                      { fixedAssetTransfer.to_locations.map((data, key) => (
                        getFieldValue('id_from_location') !== data.id_location 
                        ?
                          (
                            <Option key={key} value={data.id_location}>
                              { data.location_code + " (" + data.location_name +")"}
                            </Option> 
                          )
                        : null
                        ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.note,
              })(<TextArea 
                  disabled={ data.status === 1 ? false : true }
                  autosize={{ minRows: 4 }}
              />)}
            </Form.Item>
          </div>
        </div>    
        
             
 
        <Form.Item {...tailFormItemLayout}>
           <Button
             type="primary"
             htmlType="submit"
             style={{ marginTop: "20px", marginLeft: "50px", marginRight: "5px" }}
             loading={loading}
             disabled={
              data.status === 1
              ? false
              : true
             }
           >
             <IntlMessages id="button.submit" />
           </Button>

           {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() =>
              processFixedAssetTransfer(data.id_fixed_asset_transfer)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                permissions.includes("Confirm Fixed Asset Transfer")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

           {/* Cancel */}
          
          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 && 
              permissions.includes("Cancel Fixed Asset Transfer")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

         </Form.Item>
       </Form>
     );
  }
}

const WrappedFixedAssetTransferUpdateForm = Form.create({ name: "update_fixed_asset_transfer" })(FixedAssetTransferUpdateForm);

export default WrappedFixedAssetTransferUpdateForm;
